// define a new scroll position modification function (jQuery animate instead of jump)
controller.scrollTo(function (newScrollPos, scrollTime = 1.5) {
    $("html, body").animate({scrollTop: newScrollPos}, scrollTime*1000);
});

// const button = document.getElementById("down");
const buttons = document.getElementsByClassName("down");
