{
    buttons[i].addEventListener("click", () => {
        console.log("click");
        controller.scrollTo(document.getElementById("myAnimation"));
    });
}

/*
    scroll position = buffer + startHeight + labelTime * ( page height - dead space ) / total time
*/
let clickBuffering = false;
downArrow.addEventListener("click", () => {
    if (!clickBuffering)
    {
        clickBuffering = true;
        if(tl.labels.end - 0.1 < tl.time() && tl.time() < tl.labels.end + 0.1)
        {
            controller.scrollTo(document.getElementById("footer"));
            setTimeout(() => {clickBuffering = false;}, 1500); 
            return;
        }
        controller.scrollTo(
            10 +
            document.getElementById("myAnimation").offsetHeight +
            labels[tl.nextLabel()] *
            (document.body.clientHeight - (3 * document.getElementById("myAnimation").offsetHeight))
            / tl.duration()
        , (labels[tl.nextLabel()] - tl.time()));

        setTimeout(() => {clickBuffering = false;}, (labels[tl.nextLabel()] - tl.time())*1000); 
    }
});

var isRefreshing = false;
window.addEventListener("resize", ()=> {
    if (!isRefreshing)
    {
        setTimeout(()=>{
            if (windowStartSize.width + 200 < document.body.clientWidth ||
                windowStartSize.width - 200 > document.body.clientWidth ||
                windowStartSize.height + 200 < window.innerHeight ||
                windowStartSize.height - 200 > window.innerHeight)
            window.location.reload(false); 
            isRefreshing = true;
        }, 1000);    
    }    
});

